import MovieSection from "@/component/MovieSection";

async function fetchMovies(category) {
  const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY;
  try {
    const res = await fetch(
      `https://api.themoviedb.org/3/movie/${category}?api_key=${API_KEY}&language=ko-KR`,
      { next: { revalidate: 3600 } } // 1ÏãúÍ∞Ñ Ï∫êÏã±
    );

    if (!res.ok) {
      console.error(`Failed to fetch ${category}:`, res.statusText);
      return []; // Ïò§Î•ò Ïãú Îπà Î∞∞Ïó¥ Î∞òÌôò
    }

    const data = await res.json();
    return data.results || []; // resultsÍ∞Ä undefinedÏùº Í≤ΩÏö∞ ÎåÄÎπÑ
  } catch (err) {
    console.error(`Error fetching ${category}:`, err);
    return []; // ÎÑ§Ìä∏ÏõåÌÅ¨ ÏóêÎü¨ Ïãú Îπà Î∞∞Ïó¥ Î∞òÌôò
  }
}

export default async function MainPage() {
  // ÎπÑÎèôÍ∏∞ fetch Í≤∞Í≥ºÎ•º Ìï≠ÏÉÅ Î∞∞Ïó¥Î°ú Î≥¥Ïû•
  const [popular, nowPlaying, topRated, upcoming] = await Promise.all([
    fetchMovies("popular"),
    fetchMovies("now_playing"),
    fetchMovies("top_rated"),
    fetchMovies("upcoming"), // ‚úÖ ÏÉÅÏòÅ ÏòàÏ†ïÏûë Ï∂îÍ∞Ä
  ]);

  return (
    <main className="main-container">
      <h1 className="main-title">üé¨ ÏãúÏ≤≠ÌïòÏã† ÏòÅÌôîÏóê Î¶¨Î∑∞Î•º ÎÇ®Í≤®Ï£ºÏÑ∏Ïöî</h1>

      <MovieSection title="üî• Ïù∏Í∏∞ ÏòÅÌôî" movies={popular || []} />
      <MovieSection title="üé• ÌòÑÏû¨ ÏÉÅÏòÅÏûë" movies={nowPlaying || []} />
      <MovieSection title="‚≠ê ÌèâÏ†ê ÎÜíÏùÄ ÏòÅÌôî" movies={topRated || []} />
      <MovieSection title="‚è≥ ÏÉÅÏòÅ ÏòàÏ†ïÏûë" movies={upcoming || []} /> {/* ‚úÖ Ï∂îÍ∞Ä */}
    </main>
  );
}
